import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import { Slider } from '@mui/material';
import './SliderC.style.css';

export interface IStyle {
    maxWidth: string;
    marginRight:string;
}

export type ISliderCProps = {
    getValue: (v: number) => void;
}

const SliderC: React.FC<ISliderCProps> = ({ getValue }) => {

    const change = (e) => {
        if (e.target.value === null) {
            return;
        }
        getValue(e.target.value);
    }


    return (
        <Box width={600} className="box-wrapper">
            <Slider
                size="small"
                min={0}
                max={200}
                defaultValue={0}
                onChange={(v) => change(v)}
                aria-label="Small"
                valueLabelDisplay="auto"
            />

        </Box>
    );
}

export { SliderC };